import axios from 'axios'

export function editBaseInfo (param) {
  const url = '/api/BlogSetting/BaseInfo'
  return axios({
    method: 'PUT',
    url: url,
    headers: {
      'Content-Type': 'application/json'
    },
    data: param
  }).then((res) => {
    return Promise.resolve(res.data)
  })
}



